'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Checkbox } from '@/components/ui/checkbox'

export default function SubmitPage() {
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    fullDescription: '',
    category: '',
    price: '',
    tags: '',
    creatorName: '',
    university: '',
    major: '',
    email: '',
    whatsapp: '',
    instagram: '',
    agreeTerms: false
  })

  const [isSubmitting, setIsSubmitting] = useState(false)
  const [isSubmitted, setIsSubmitted] = useState(false)

  const handleInputChange = (field: string, value: string | boolean) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }))
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsSubmitting(true)

    // Simulasi submit (dalam implementasi nyata, ini akan mengirim ke API)
    await new Promise(resolve => setTimeout(resolve, 2000))
    
    setIsSubmitting(false)
    setIsSubmitted(true)
  }

  if (isSubmitted) {
    return (
      <div className="min-h-screen bg-gray-50 py-8">
        <div className="max-w-2xl mx-auto px-4 sm:px-6 lg:px-8">
          <Card className="text-center p-8">
            <div className="text-6xl mb-4">✅</div>
            <CardHeader>
              <CardTitle className="text-2xl text-green-600">
                Karya Berhasil Disubmit!
              </CardTitle>
              <CardDescription className="text-lg">
                Terima kasih telah mengirimkan karya Anda. Tim kami akan meninjau dan menghubungi Anda dalam 1-2 hari kerja.
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                <p className="text-gray-600">
                  Kami akan mengirimkan konfirmasi ke email: <strong>{formData.email}</strong>
                </p>
                <div className="flex flex-col sm:flex-row gap-4 justify-center">
                  <Button onClick={() => {
                    setIsSubmitted(false)
                    setFormData({
                      title: '',
                      description: '',
                      fullDescription: '',
                      category: '',
                      price: '',
                      tags: '',
                      creatorName: '',
                      university: '',
                      major: '',
                      email: '',
                      whatsapp: '',
                      instagram: '',
                      agreeTerms: false
                    })
                  }}>
                    Submit Karya Lain
                  </Button>
                  <Button variant="outline" asChild>
                    <a href="/works">Lihat Karya Lain</a>
                  </Button>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="mb-8 text-center">
          <h1 className="text-3xl md:text-4xl font-bold text-gray-900 mb-4">
            Submit Karya Anda
          </h1>
          <p className="text-xl text-gray-600 max-w-2xl mx-auto">
            Bagikan karya kreatif Anda dan mulai menghasilkan melalui donasi, komisi, atau penjualan langsung
          </p>
        </div>

        {/* Guidelines */}
        <Card className="mb-8">
          <CardHeader>
            <CardTitle className="text-lg">📋 Panduan Submit Karya</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
              <div>
                <h4 className="font-semibold mb-2">✅ Yang Diterima:</h4>
                <ul className="space-y-1 text-gray-600">
                  <li>• Karya original buatan sendiri</li>
                  <li>• Desain grafis, logo, ilustrasi</li>
                  <li>• Artikel, blog post, riset</li>
                  <li>• Source code program/aplikasi</li>
                  <li>• Musik instrumental original</li>
                  <li>• Template presentasi/dokumen</li>
                </ul>
              </div>
              <div>
                <h4 className="font-semibold mb-2">❌ Yang Tidak Diterima:</h4>
                <ul className="space-y-1 text-gray-600">
                  <li>• Karya hasil plagiat/copy</li>
                  <li>• Konten dewasa/tidak pantas</li>
                  <li>• Karya yang melanggar hak cipta</li>
                  <li>• Spam atau konten berkualitas rendah</li>
                </ul>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Form */}
        <form onSubmit={handleSubmit} className="space-y-8">
          {/* Informasi Karya */}
          <Card>
            <CardHeader>
              <CardTitle>🎨 Informasi Karya</CardTitle>
              <CardDescription>
                Berikan detail lengkap tentang karya yang ingin Anda submit
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div>
                <Label htmlFor="title">Judul Karya *</Label>
                <Input
                  id="title"
                  value={formData.title}
                  onChange={(e) => handleInputChange('title', e.target.value)}
                  placeholder="Contoh: Desain Logo Modern untuk Startup"
                  required
                />
              </div>

              <div>
                <Label htmlFor="category">Kategori *</Label>
                <Select value={formData.category} onValueChange={(value) => handleInputChange('category', value)}>
                  <SelectTrigger>
                    <SelectValue placeholder="Pilih kategori karya" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Desain">Desain</SelectItem>
                    <SelectItem value="Artikel">Artikel</SelectItem>
                    <SelectItem value="Program">Program</SelectItem>
                    <SelectItem value="Musik">Musik</SelectItem>
                  </SelectContent>
                </Select>
              </div>

              <div>
                <Label htmlFor="description">Deskripsi Singkat *</Label>
                <Textarea
                  id="description"
                  value={formData.description}
                  onChange={(e) => handleInputChange('description', e.target.value)}
                  placeholder="Jelaskan karya Anda dalam 1-2 kalimat"
                  rows={3}
                  required
                />
              </div>

              <div>
                <Label htmlFor="fullDescription">Deskripsi Lengkap *</Label>
                <Textarea
                  id="fullDescription"
                  value={formData.fullDescription}
                  onChange={(e) => handleInputChange('fullDescription', e.target.value)}
                  placeholder="Jelaskan detail karya, apa yang didapat pembeli, spesifikasi teknis, dll."
                  rows={6}
                  required
                />
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <Label htmlFor="price">Harga *</Label>
                  <Input
                    id="price"
                    value={formData.price}
                    onChange={(e) => handleInputChange('price', e.target.value)}
                    placeholder="Contoh: Rp 150.000"
                    required
                  />
                </div>
                <div>
                  <Label htmlFor="tags">Tags (pisahkan dengan koma)</Label>
                  <Input
                    id="tags"
                    value={formData.tags}
                    onChange={(e) => handleInputChange('tags', e.target.value)}
                    placeholder="Contoh: logo, modern, minimalis"
                  />
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Informasi Kreator */}
          <Card>
            <CardHeader>
              <CardTitle>👤 Informasi Kreator</CardTitle>
              <CardDescription>
                Informasi tentang Anda sebagai pembuat karya
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div>
                <Label htmlFor="creatorName">Nama Lengkap *</Label>
                <Input
                  id="creatorName"
                  value={formData.creatorName}
                  onChange={(e) => handleInputChange('creatorName', e.target.value)}
                  placeholder="Nama lengkap Anda"
                  required
                />
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <Label htmlFor="university">Universitas/Sekolah *</Label>
                  <Input
                    id="university"
                    value={formData.university}
                    onChange={(e) => handleInputChange('university', e.target.value)}
                    placeholder="Contoh: Universitas Indonesia"
                    required
                  />
                </div>
                <div>
                  <Label htmlFor="major">Jurusan/Program Studi *</Label>
                  <Input
                    id="major"
                    value={formData.major}
                    onChange={(e) => handleInputChange('major', e.target.value)}
                    placeholder="Contoh: Desain Komunikasi Visual"
                    required
                  />
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Kontak */}
          <Card>
            <CardHeader>
              <CardTitle>📞 Informasi Kontak</CardTitle>
              <CardDescription>
                Informasi kontak untuk pembeli yang tertarik dengan karya Anda
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div>
                <Label htmlFor="email">Email *</Label>
                <Input
                  id="email"
                  type="email"
                  value={formData.email}
                  onChange={(e) => handleInputChange('email', e.target.value)}
                  placeholder="email@example.com"
                  required
                />
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <Label htmlFor="whatsapp">WhatsApp *</Label>
                  <Input
                    id="whatsapp"
                    value={formData.whatsapp}
                    onChange={(e) => handleInputChange('whatsapp', e.target.value)}
                    placeholder="+62812-3456-7890"
                    required
                  />
                </div>
                <div>
                  <Label htmlFor="instagram">Instagram (opsional)</Label>
                  <Input
                    id="instagram"
                    value={formData.instagram}
                    onChange={(e) => handleInputChange('instagram', e.target.value)}
                    placeholder="@username"
                  />
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Terms */}
          <Card>
            <CardContent className="pt-6">
              <div className="flex items-start space-x-2">
                <Checkbox
                  id="terms"
                  checked={formData.agreeTerms}
                  onCheckedChange={(checked) => handleInputChange('agreeTerms', checked as boolean)}
                />
                <div className="grid gap-1.5 leading-none">
                  <Label
                    htmlFor="terms"
                    className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70"
                  >
                    Saya setuju dengan syarat dan ketentuan *
                  </Label>
                  <p className="text-xs text-muted-foreground">
                    Dengan mencentang ini, Anda menyetujui bahwa karya yang disubmit adalah original dan tidak melanggar hak cipta.
                  </p>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Submit Button */}
          <div className="text-center">
            <Button 
              type="submit" 
              size="lg" 
              disabled={!formData.agreeTerms || isSubmitting}
              className="px-8"
            >
              {isSubmitting ? 'Mengirim...' : 'Submit Karya'}
            </Button>
          </div>
        </form>
      </div>
    </div>
  )
}
